body {
  --hover-color: #00acdc;
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo-link {
  width: 40px;
  height: 40px;
}

.img {
  width: 100%;
  height: 100%;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.link-container {
  margin-left: 20px;
  position: relative;
}

.link-container:first-child {
  margin-left: 0;
}

.nav-link {
  color: #000;
  white-space: nowrap;
  text-transform: uppercase;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .5s;
  display: flex;
}

.nav-link:hover, .nav-link.is-active {
  color: var(--hover-color);
}

.nav-link.is-active:after {
  content: "";
  pointer-events: none;
  background: var(--hover-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

